import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';
import {Brand} from '../../../data/brand/brand';
import {BrandService} from '../../../data/brand/brand.service';
import {MatPaginator} from '@angular/material/paginator';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-brands',
  templateUrl: './brands.component.html',
  styleUrls: ['./brands.component.css']
})
export class BrandsComponent implements OnInit {

  table_datasource: MatTableDataSource<Brand> = new MatTableDataSource<Brand>();
  table_columns: string[] = ['id', 'name', ' '];
  brands: Brand[] = [];
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public brandService: BrandService, private dialog: MatDialog) { }

  ngOnInit(): void {
    this.brandService.getAll();
    this.brandService.entities$.subscribe(data => this.table_datasource.data = data);
    this.table_datasource.paginator = this.paginator;
  }

  async newBrand() {
    await this.dialog.open(NewBrandComponent).afterClosed().subscribe(async () => await this.ngOnInit());
  }

  edit(element: Brand) {
    this.dialog.open(EditBrandComponent, {
      data: element
    }).afterClosed().subscribe(async () => {
      this.ngOnInit();
    });
  }

  delete(element: Brand) {
    this.dialog.open(DeleteBrandComponent, {
      data: element
    }).afterClosed().subscribe(async () => {
      this.ngOnInit();
    });
  }
}

@Component({
  template: `
  <div class="container">
    <h4>Editing Brand {{data.name}}</h4>
      <mat-form-field class="col-md-12">
      	<mat-label>Brand Name</mat-label>
      	<input matInput [(ngModel)]="name" />
      </mat-form-field>
    <div class="row">
      <div class="col-6">
        <button mat-raised-button color="primary" (click)="save()">Save</button>
      </div>
      <div class="col-6">
        <button mat-raised-button color="warn" (click)="close()">Close</button>
      </div>
    </div>
  </div>
  `,
  styleUrls: ['brands.component.css']
})
export class EditBrandComponent {
  name: string;

  constructor(public dialogRef: MatDialogRef<EditBrandComponent>, private brandService: BrandService, @Inject(MAT_DIALOG_DATA) public data: Brand) {
    this.name = data.name;
  }

  save() {
    try {
      console.log(`editted ${this.data.name}`);
      this.brandService.update({id: this.data.id, name: this.name});
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }
}

@Component({
  template: `
    <div class="container">
      <h4>Are you sure you want to <b>Delete</b> {{data.name}}</h4>
      <div class="row">
        <div class="col-6">
          <button mat-raised-button color="warn" (click)="delete()">Yes, Delete</button>
        </div>
        <div class="col-6">
          <button mat-raised-button color="primary" (click)="close()">No, Do Not</button>
        </div>
      </div>
    </div>
  `,
  styleUrls: ['brands.component.css']
})
export class DeleteBrandComponent {

  constructor(public dialogRef: MatDialogRef<DeleteBrandComponent>, private brandService: BrandService, @Inject(MAT_DIALOG_DATA) public data: Brand) {}

  delete() {
    try {
      console.log(`deleted ${this.data.name}`);
      this.brandService.delete(this.data);
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }

}

@Component({
  template: `
  <div class="container">
    <mat-form-field class="col-md-6">
    	<mat-label>Brand Name</mat-label>
      <input matInput [(ngModel)]="name"/>
    </mat-form-field>
    <div class="row">
      <div class="col-6">
        <button mat-raised-button color="primary" (click)="save()">Save</button>
      </div>
      <div class="col-6">
        <button mat-raised-button color="warn" (click)="close()">Close</button>
      </div>
    </div>
  </div>`,
  styleUrls: ['brands.component.css']
})
export class NewBrandComponent {
  name = '';

  constructor(private brandService: BrandService, public dialogRef: MatDialogRef<NewBrandComponent>) {
  }

  save() {
    console.log(this.name);
    if (this.name === '') {
      return;
    }
    try {
      this.brandService.add({name: this.name});
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }

}
