import { Component, OnInit } from '@angular/core';
import {AuthService} from "../../auth/auth/auth.service";
import {BrandService} from "../../../data/brand/brand.service";
import {ProductService} from "../../../data/product/product.service";
import {TypeService} from "../../../data/type/type.service";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(public authService: AuthService, public brandService: BrandService, public productService: ProductService, public typeService: TypeService) { }

  ngOnInit(): void {
  }

  setbrand(id: number | string) {
    let name
    this.brandService.entities$.subscribe(data => {
      data.forEach(i => {
        if (i.id === id) {
          name = i.name;
        }
      });
    })
    return name;
  }

}
