import { Injectable } from '@angular/core';
import {Product} from '../data/product/product';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  private items: CartItem[] = [];

  constructor() { }

  clear() {
    this.items = [];
  }

  public getCartItems(): CartItem[] {
    return this.items;
  }

  public add(product: Product, quantity: number): any {
    const p = this.items.findIndex(i => product.id === i.product.id);
    if (p <= 0) {
      return this.items.push({product, quantity});
    } else {
      if (this.items[p].quantity < quantity) {
        throw new Error('sorry we you can buy more than we than we have.');
      }
      return this.items[p].quantity += quantity;
    }
  }

  public remove(item: CartItem) {
    if (item == null) {
      return;
    } else {
      return this.items.splice(this.items.findIndex(i => i === item), 1);
    }
  }

  update(id: number, quantity: number) {
    const position = this.items.findIndex(i => i.product.id === id);
    if (position <= 0) {
      return;
    } else {
      return this.items[id].quantity = quantity;
    }
  }
}

export interface CartItem {
  product: Product;
  quantity: number;
}
