import { Component, OnInit } from '@angular/core';
import {ProductService} from "../../data/product/product.service";
import {BrandService} from "../../data/brand/brand.service";

@Component({
  selector: 'app-wheels',
  templateUrl: './wheels.component.html',
  styleUrls: ['./wheels.component.css']
})
export class WheelsComponent implements OnInit {

  constructor(public productService: ProductService, public brandService: BrandService) { }

  ngOnInit(): void {
    this.productService.getAll();
    this.brandService.getAll();
  }

  setbrand(id: number | string) {
    let name
    this.brandService.entities$.subscribe(data => {
      data.forEach(i => {
        if (i.id === id) {
          name = i.name;
        }
      })
    })
    return name
  }

}
