import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {ProductService} from '../../../data/product/product.service';
import {MatTableDataSource} from '@angular/material/table';
import {Product} from '../../../data/product/product';
import {MatPaginator} from '@angular/material/paginator';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {AddProductComponent} from '../add-product/add-product.component';
import {TypeService} from '../../../data/type/type.service';
import {BrandService} from '../../../data/brand/brand.service';

@Component({
  selector: 'app-view-products',
  templateUrl: './view-products.component.html',
  styleUrls: ['./view-products.component.css']
})
export class ViewProductsComponent implements OnInit {
  table_columns: string[] = ['name', 'tyre_code', 'price', 'description', 'quantity', 'brand', 'type', ' '];
  table_datasource: MatTableDataSource<Product> = new MatTableDataSource<Product>();
  info: Product[] = [];

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public productService: ProductService, public dialog: MatDialog) {
  }

  async ngOnInit() {
    await this.productService.getAll().subscribe((data) => this.table_datasource.data = data);
  }

  edit(element: Product) {
    this.dialog.open(EditProductComponent, {
      data: element
    }).afterClosed().subscribe(async () => {
      await this.ngOnInit();
    });
  }

  delete(element: any) {
    this.dialog.open(DeleteProductComponent, {
      data: element
    }).afterClosed().subscribe(async () => {
      await this.ngOnInit();
    });
  }

  async addProductDialog() {
    this.dialog.open(AddProductComponent).afterClosed().subscribe(async () => {
      await this.ngOnInit();
    });
  }
}

@Component({
  template:
  `
    <div class="container">
      <h4>Editing {{data.name}}</h4>
      <mat-divider></mat-divider>
      <div class="container">
        <div class="row">
          <mat-form-field class="col-md-6">
            <mat-label>Name</mat-label>
            <input matInput [(ngModel)]="prod.name" />
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Tyre Code</mat-label>
            <input matInput [(ngModel)]="prod.tyre_code"/>
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Brand</mat-label>
            <mat-select [(ngModel)]="prod.brand" [value]="prod.brand">
              <mat-option *ngFor="let i of (brandService.entities$ | async)" value="{{i.id}}">{{i.name}}</mat-option>
            </mat-select>
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Type</mat-label>
            <mat-select [(ngModel)]="prod.type" [value]="prod.type">
              <mat-option *ngFor="let i of (typeService.entities$ | async)" value="{{i.id}}">{{i.name}}</mat-option>
            </mat-select>
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Price</mat-label>
            <input matInput [(ngModel)]="prod.price" type="number"/>
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Description</mat-label>
            <input matInput [(ngModel)]="prod.description"/>
          </mat-form-field>
          <mat-form-field class="col-md-6">
            <mat-label>Quantity</mat-label>
            <input matInput [(ngModel)]="prod.quantity" type="number"/>
          </mat-form-field>
        </div>
      <mat-divider></mat-divider>
      <div class="row">
        <div class="col-6">
          <button mat-raised-button color="primary" (click)="save()">Save</button>
        </div>
        <div class="col-6">
          <button mat-raised-button color="warn" (click)="close()">Close</button>
        </div>
      </div>
    </div>
  `,
  styleUrls: ['view-products.component.css']
})
export class EditProductComponent {

  prod: Product;

  constructor(public dialogRef: MatDialogRef<EditProductComponent>, public brandService: BrandService, public typeService: TypeService, private productService: ProductService, @Inject(MAT_DIALOG_DATA) public data: Product) {
    this.prod = {
      quantity: data.quantity,
      description: data.description,
      brand: data.brand,
      tyre_code: data.tyre_code,
      name: data.name,
      type: data.type,
      id: data.id,
      price: data.price
    };
    console.log(this.prod);
  }

  save() {
    try {
      console.log(this.prod);
      this.productService.update(this.prod);
      this.dialogRef.close();
    }catch (e) {
      console.log(e);
    }

  }

  close() {
    this.dialogRef.close();
  }

}

@Component({
  template: `
  <div class="container">
    <p class="text-danger h4"><b>Are you sure you want to delete {{data.name}} from the database</b></p>
    <div class="row">
      <div class="col-6">
        <button mat-raised-button color="warn" (click)="delete()">Yes, Delete</button>
      </div>
      <div class="col-6">
        <button mat-raised-button color="primary" (click)="close()">No, Do Not</button>
      </div>
    </div>
  </div>
  `,
  styleUrls: ['view-products.component.css']
})
export class DeleteProductComponent {

  constructor(public dialogRef: MatDialogRef<DeleteProductComponent>, private productService: ProductService, @Inject(MAT_DIALOG_DATA) public data: Product) {
  }

  delete() {
    try {
      console.log(`deleted ${this.data.name}`);
      this.productService.delete(this.data);
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }

}
