import {Component, Inject, OnInit} from '@angular/core';
import {ProductService} from '../../../data/product/product.service';
import {BrandService} from '../../../data/brand/brand.service';
import {TypeService} from '../../../data/type/type.service';
import {MatDialogRef} from '@angular/material/dialog';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css']
})
export class AddProductComponent implements OnInit {

  form: FormGroup;

  constructor(private fb: FormBuilder, public productService: ProductService, public brandService: BrandService, public typeService: TypeService, private dialogRef: MatDialogRef<AddProductComponent>) { }

  ngOnInit(): void {
    this.brandService.getAll();
    this.productService.getAll();
    this.typeService.getAll();
    this.form = this.fb.group({
      name: new FormControl(),
      tyre_code: new FormControl(),
      brand: new FormControl(),
      type: new FormControl(),
      price: new FormControl(),
      description: new FormControl(),
      quantity: new FormControl(),
    });
  }

  save() {
    console.log(this.form.value)
    try {
      this.productService.add(this.form.value);
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }

}
