import { Component, OnInit } from '@angular/core';
import { CartService } from './services/cart.service';
import { MatDialog } from '@angular/material/dialog';
import { SigninComponent } from './ui/auth/signin/signin.component';
import { AuthService } from './ui/auth/auth/auth.service';
import { SignoutComponent } from './ui/auth/signout/signout.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  constructor(public cartService: CartService, private dialog: MatDialog, public authService: AuthService) {}

  date = new Date();

  ngOnInit(): void {}

  loginDialog() {
    this.dialog.open(SigninComponent);
  }

  logout() {
    this.dialog.open(SignoutComponent).afterClosed().subscribe((data: boolean) => {
      if (data && this.authService.authenticated) {
        this.authService.logout();
      }
    });
  }

}

