import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import { FlexLayoutModule } from '@angular/flex-layout';
import {Routes} from '@angular/router';

import {StoreModule} from '@ngrx/store';
import {StoreDevtoolsModule} from '@ngrx/store-devtools';
import {EffectsModule} from '@ngrx/effects';
import {DefaultDataServiceConfig, EntityDataModule, Pluralizer} from '@ngrx/data';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {environment} from '../environments/environment';
import { AppComponent } from './app.component';
import { entityConfig } from './data/entity-metadata';
import { HomeComponent } from './ui/home/home.component';
import { TiresComponent } from './ui/tires/tires.component';
import { WheelsComponent } from './ui/wheels/wheels.component';
import { ServicesComponent } from './ui/services/services.component';
import { ContactusComponent } from './ui/contactus/contactus.component';
import { AboutusComponent } from './ui/aboutus/aboutus.component';
import { CartComponent } from './utils/cart/cart.component';
import { AdToCartComponent } from './ui/ad-to-cart/ad-to-cart.component';
import { SigninComponent } from './ui/auth/signin/signin.component';
import { SignoutComponent } from './ui/auth/signout/signout.component';
import { SignupComponent } from './ui/auth/signup/signup.component';
import {DashboardComponent, IconButtonComponent} from './ui/dashboard/dashboard.component';
import { OrdersComponent } from './ui/dashboard/orders/orders.component';
import { AddressbookComponent } from './ui/dashboard/addressbook/addressbook.component';
import {EditUserComponent, MyaccountComponent} from './ui/dashboard/myaccount/myaccount.component';

import {MatToolbarModule} from '@angular/material/toolbar';
import {MatButtonModule} from '@angular/material/button';
import {MatIconModule} from '@angular/material/icon';
import {MatBadgeModule} from '@angular/material/badge';
import {MatInputModule} from '@angular/material/input';
import {MatMenuModule} from '@angular/material/menu';
import { MatDialogModule} from '@angular/material/dialog';
import {MatCardModule} from '@angular/material/card';
import {MatDividerModule} from '@angular/material/divider';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import {MatSelectModule} from '@angular/material/select';
import {AuthGuardService} from './ui/auth/services/auth-guard.service';
import {MatSnackBarModule} from '@angular/material/snack-bar';
import { AddProductComponent } from './ui/admin/add-product/add-product.component';
import {
  DeleteProductComponent,
  EditProductComponent,
  ViewProductsComponent
} from './ui/admin/view-products/view-products.component';
import * as AdminHome from './ui/admin/home/home.component';
import {AdminAuthGuardService} from './ui/admin/services/auth-guard.service';
import { OverviewComponent } from './ui/admin/overview/overview.component';
import {
  BrandsComponent,
  DeleteBrandComponent,
  EditBrandComponent,
  NewBrandComponent
} from './ui/admin/brands/brands.component';
import {
  DeleteTypeComponent,
  EditTypeComponent,
  NewTyreTypeComponent,
  TypesComponent
} from './ui/admin/types/types.component';
import {MatTableModule} from "@angular/material/table";
import {MatPaginatorModule} from "@angular/material/paginator";
import {PutInterceptorInterceptor} from "./utils/put-interceptor.interceptor";

export const AppRoutes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'admin',
    component: AdminHome.HomeComponent,
    canActivate: [AdminAuthGuardService],
    children: [
      {
        path: '',
        redirectTo: 'products',
        pathMatch: 'full'
      },
      {
        path: 'brands',
        component: BrandsComponent
      },
      {
        path: 'types',
        component: TypesComponent
      },
      {
        path: 'products',
        component: ViewProductsComponent
      }
    ]
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: '',
        redirectTo: 'account',
        pathMatch: 'full'
      },
      {
        path: 'account',
        component: MyaccountComponent
      },
      {
        path: 'orders',
        component: OrdersComponent
      },
      {
        path: 'address',
        component: AddressbookComponent
      }
    ]
  },
  {
    path: 'tyres',
    component: TiresComponent
  },
  {
    path: 'wheels',
    component: WheelsComponent
  },
  {
    path: 'services',
    component: ServicesComponent
  },
  {
    path: 'contactus',
    component: ContactusComponent
  },
  {
    path: 'aboutus',
    component: AboutusComponent
  },
  {
    path: 'cart',
    component: CartComponent
  },
  {
    path: 'signin',
    component: SigninComponent
  },
  {
    path: 'signout',
    component: SignoutComponent
  },
  {
    path: 'signup',
    component: SignupComponent
  }
];

const dataServiceConfig: DefaultDataServiceConfig = {
  root: 'https://noblesiteapi.herokuapp.com/api/',
  // root: 'http://localhost:8000/api/',
};

const pluralizer: Pluralizer = {
  pluralize(name: string): string {
    return name;
  }
};

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    TiresComponent,
    WheelsComponent,
    ServicesComponent,
    ContactusComponent,
    AboutusComponent,
    CartComponent,
    AdToCartComponent,
    SigninComponent,
    SignoutComponent,
    SignupComponent,
    DashboardComponent,
    OrdersComponent,
    IconButtonComponent,
    EditUserComponent,
    AddressbookComponent,
    MyaccountComponent,
    AddProductComponent,
    ViewProductsComponent,
    HomeComponent,
    OverviewComponent,
    AdminHome.HomeComponent,
    BrandsComponent,
    TypesComponent,
    EditProductComponent,
    DeleteProductComponent,
    EditBrandComponent,
    DeleteBrandComponent,
    EditTypeComponent,
    DeleteTypeComponent,
    NewBrandComponent,
    NewTyreTypeComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    FlexLayoutModule,
    HttpClientModule,
    RouterModule.forRoot(AppRoutes),
    StoreModule.forRoot({}),
    EffectsModule.forRoot([]),
    EntityDataModule.forRoot(entityConfig),
    StoreDevtoolsModule.instrument({
      maxAge: 25,
      logOnly: environment.production,
    }),
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    MatBadgeModule,
    MatCardModule,
    MatDividerModule,
    MatFormFieldModule,
    MatInputModule,
    MatAutocompleteModule,
    MatSelectModule,
    MatMenuModule,
    MatDialogModule,
    MatSnackBarModule,
    MatTableModule,
    MatPaginatorModule,
  ],
  providers: [
    {
      provide: DefaultDataServiceConfig,
      useValue: dataServiceConfig
    },
    {
      provide: Pluralizer,
      useValue: pluralizer
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: PutInterceptorInterceptor,
      multi: true
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
