import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {UsersService} from '../../../data/users/users.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  form: FormGroup;
  confirm_pass = '';
  err: string;

  constructor(private fb: FormBuilder, private usersService: UsersService, private router: Router) { }

  ngOnInit(): void {
    this.form = this.fb.group({
      username: new FormControl(''),
      first_name: new FormControl(''),
      last_name: new FormControl(''),
      email: new FormControl(''),
      password: new FormControl(''),
    });
  }

  checkPassword() {
    this.clearErr();
    const p1 = this.form.controls.password.value;
    const p2 = this.confirm_pass;
    if (p1 === '' || p2 === '') {
      return this.err = 'one password fields is empty';
    } else if (p1 !== p2) {
      return this.err = 'your passwords are no matching';
    }
  }

  clearErr() {
    this.err = '';
  }

  async signup() {
    this.checkPassword();
    if (this.err !== '') {
      return;
    }
    try {
      await this.usersService.add(this.form.value);
      await this.router.navigateByUrl('signin');
    } catch (e) {
      console.log(e);
    }
  }

}
