import {Component, Inject, OnInit} from '@angular/core';
import {AuthService} from '../../auth/auth/auth.service';
import {Users} from '../../../data/users/users';
import {MAT_DIALOG_DATA, MatDialog} from '@angular/material/dialog';
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import { UsersService } from '../../../data/users/users.service';

@Component({
  selector: 'app-myaccount',
  templateUrl: './myaccount.component.html',
  styleUrls: ['./myaccount.component.css']
})
export class MyaccountComponent implements OnInit {

  constructor(public authService: AuthService, private dialog: MatDialog) { }

  ngOnInit(): void {
    this.authService.getLoggedInUser();
  }

    accountEditDialog() {
        this.dialog.open(EditUserComponent, {data: this.authService.getLoggedInUser()});
    }
}

@Component({
  template: `
    <div class="container">
      <div class="text-center"><p class="mat-h2">Edit User Details</p></div>
      <mat-form-field class="col-md-12" [formGroup]="form">
      	<mat-label>First Name</mat-label>
      	<input matInput formControlName="first_name" />
      </mat-form-field>
      <mat-form-field class="col-md-6" [formGroup]="form">
      	<mat-label>Last Name</mat-label>
      	<input matInput formControlName="last_name" />
      </mat-form-field>
      <mat-form-field class="col-md-6" [formGroup]="form">
      	<mat-label>Email</mat-label>
      	<input matInput formControlName="email" type="email" />
      </mat-form-field>
      <div class="row">
        <div class="spacer"></div>
        <button class="btn btn-danger" (click)="save()" >Save</button>
        <div class="spacer"></div>
      </div>
    </div>

  `,
  styleUrls: ['myaccount.component.css']
})
export class EditUserComponent {
  form: FormGroup;

  constructor(private authService: AuthService, private fb: FormBuilder, @Inject(MAT_DIALOG_DATA) private data: Users, private usersService: UsersService) {
    this.form = this.fb.group({
      first_name: new FormControl(this.authService.getLoggedInUser().first_name),
      last_name: new FormControl(this.authService.getLoggedInUser().last_name),
      email: new FormControl(this.authService.getLoggedInUser().email),
    });
  }

  save() {
    const user: Users = this.authService.getLoggedInUser();
    user.first_name = this.form.controls.first_name.value;
    user.last_name = this.form.controls.last_name.value;
    user.email = this.form.controls.email.value;
    console.log(user);
    this.usersService.update(user).subscribe(data => console.log(data));
  }

}
