import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';
import {Brand} from '../../../data/brand/brand';
import {TypeService} from '../../../data/type/type.service';
import {MatPaginator} from '@angular/material/paginator';
import {Type} from '../../../data/type/type';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {BrandService} from '../../../data/brand/brand.service';

@Component({
  selector: 'app-types',
  templateUrl: './types.component.html',
  styleUrls: ['./types.component.css']
})
export class TypesComponent implements OnInit {
  table_columns: string[] = ['id', 'name', ' '];
  table_datasource: MatTableDataSource<Brand> = new MatTableDataSource<Brand>();

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  constructor(public typeService: TypeService, private dialog: MatDialog) { }

  ngOnInit(): void {
    this.typeService.getAll().subscribe(data => this.table_datasource.data = data);
    this.table_datasource.paginator = this.paginator;
  }

  edit(element: Type) {
    this.dialog.open(EditTypeComponent, {
      data: element
    }).afterClosed().subscribe(async () => this.ngOnInit());
  }

  delete(element: Type) {
    this.dialog.open(DeleteTypeComponent, {
      data: element
    }).afterClosed().subscribe(async () => this.ngOnInit());
  }

  newType() {
    this.dialog.open(NewTyreTypeComponent).afterClosed().subscribe(async () => this.ngOnInit());
  }
}

@Component({
  template: `<div class="container">
      <h4>Editing Tyre Type {{data.name}}</h4>
      <mat-form-field class="col-md-6">
      	<mat-label>Tyre Type</mat-label>
      	<input matInput [(ngModel)]="name" />
      </mat-form-field>
      <div class="row">
        <div class="col-6">
          <button mat-raised-button color="primary" (click)="save()">Save</button>
        </div>
        <div class="col-6">
          <button mat-raised-button color="warn" (click)="close()">Close</button>
        </div>
      </div>
    </div>`,
  styleUrls: ['types.component.css']
})
export class EditTypeComponent {
  name = '';
  constructor(public dialogRef: MatDialogRef<EditTypeComponent>, private typeService: TypeService, @Inject(MAT_DIALOG_DATA) public data: Brand) {
    this.name = data.name;
  }

  save() {
    try {
      console.log(`editted ${this.data.name}`);
      this.typeService.update({id: this.data.id, name: this.name});
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }
}

@Component({
  template: `
    <div class="container">
      <h4 class="text-danger">Are you sure you want to delete {{data.name}}</h4>
      <div class="row">
        <div class="col-6">
          <button mat-raised-button color="warn" (click)="delete()">Yes, Delete</button>
        </div>
        <div class="col-6">
          <button mat-raised-button color="primary" (click)="close()">No, Do Not</button>
        </div>
      </div>
    </div>
  `,
  styleUrls: ['types.component.css']
})
export class DeleteTypeComponent {

  constructor(public dialogRef: MatDialogRef<DeleteTypeComponent>, private typeService: TypeService, @Inject(MAT_DIALOG_DATA) public data: Brand) {}

  delete() {
    try {
      console.log(`deleted ${this.data.name}`);
      this.typeService.delete(this.data);
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }
}

@Component({
  template: `
    <div class="container">
      <h4>New Tyre Type</h4>
      <div class="row">
        <mat-form-field class="col-md-6">
        	<mat-label></mat-label>
        	<input matInput [(ngModel)]="name" />
        </mat-form-field>
      </div>
      <div class="row">
        <div class="col-6">
          <button mat-raised-button color="primary" (click)="save()">Save</button>
        </div>
        <div class="col-6">
          <button mat-raised-button color="warn" (click)="close()">Close</button>
        </div>
      </div>
    </div>
  `,
  styleUrls: ['types.component.css']
})
export class NewTyreTypeComponent {
  name = '';
  constructor(private typeService: TypeService, private dialogRef: MatDialogRef<NewTyreTypeComponent>) {}

  save() {
    if (this.name === '') {
      return;
    }
    try {
      this.typeService.add({name: this.name});
      this.dialogRef.close();
    } catch (e) {
      console.log(e);
    }
  }

  close() {
    this.dialogRef.close();
  }

}
