import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ProductService } from '../../data/product/product.service';
import {BrandService} from "../../data/brand/brand.service";
import {AuthService} from "../auth/auth/auth.service";
import {TypeService} from "../../data/type/type.service";

@Component({
  selector: 'app-tires',
  templateUrl: './tires.component.html',
  styleUrls: ['./tires.component.css']
})
export class TiresComponent implements OnInit {
  tyre_types = new FormControl();
  tyre_brand = new FormControl();
  currency = new FormControl();

  constructor(public productService: ProductService, public brandService: BrandService, public typeService: TypeService, public authService: AuthService) { }

  ngOnInit(): void {
    this.productService.getAll();
    this.brandService.getAll();
    this.typeService.getAll()
  }

  setbrand(id: number | string) {
    let name
    this.brandService.entities$.subscribe(data => {
      data.forEach(i => {
        if (i.id === id) {
          name = i.name;
        }
      });
    })
    return name;
  }

}

class Pic {
  name!: string;
  image: string;
}
